import axios from 'axios';

// Default headers for all requests
const defaultHeaders: Record<string, string> = {
  'Content-Type': 'application/json',
  'Accept': 'application/json',
  'X-Requested-With': 'XMLHttpRequest',
};

// Create an axios instance with default config
const api = axios.create({
  baseURL: import.meta.env.VITE_API_URL || 'https://mic-789615430879.asia-south1.run.app',
  withCredentials: true, // Important for sending cookies with CORS
  headers: defaultHeaders,
  timeout: 30000, // 30 seconds timeout
});

// Add a request interceptor to include auth token and handle CORS
api.interceptors.request.use(
  (config) => {
    // Add any auth tokens if needed
    // const token = localStorage.getItem('token');
    // if (token) {
    //   config.headers.Authorization = `Bearer ${token}`;
    // }
    
    // Ensure headers are properly set for CORS
    config.headers = {
      ...defaultHeaders,
      ...(config.headers as Record<string, string>),
    } as any; // Type assertion needed for Axios headers
    
    // For non-GET requests, ensure we have the right content type
    if (config.method !== 'get' && config.data) {
      config.headers['Content-Type'] = 'application/json';
    }
    
    return config;
  },
  (error) => {
    console.error('Request error:', error);
    return Promise.reject(error);
  }
);

// Add a response interceptor to handle errors and CORS
api.interceptors.response.use(
  (response) => {
    // Handle successful responses
    return response;
  },
  (error) => {
    // Handle CORS errors
    if (error.code === 'ERR_NETWORK') {
      console.error('Network Error: Unable to connect to the server. Please check your internet connection.');
      return Promise.reject({
        message: 'Unable to connect to the server. Please check your internet connection.',
        isNetworkError: true
      });
    }
    
    // Handle CORS preflight errors
    if (error.response && error.response.status === 0) {
      console.error('CORS Error: The request was blocked due to CORS policy.');
      return Promise.reject({
        message: 'The request was blocked by CORS policy. Please try again later or contact support.',
        isCorsError: true
      });
    }
    
    // Handle server errors
    if (error.response) {
      const { status, data } = error.response;
      console.error(`API Error ${status}:`, data);
      
      // Handle specific error statuses
      if (status === 401) {
        // Handle unauthorized
      } else if (status === 403) {
        // Handle forbidden
      } else if (status >= 500) {
        // Handle server errors
        return Promise.reject({
          message: 'A server error occurred. Please try again later.',
          status,
          data
        });
      }
    }
    
    return Promise.reject(error);
  }
);

export const getRates = async (country: string, weight: number) => {
  try {
    const response = await api.post('/api/get-rates', { country, weight });
    return response.data;
  } catch (error) {
    console.error('Error fetching rates:', error);
    throw error;
  }
};

export const getCarriers = async () => {
  try {
    const response = await api.get('/api/carriers');
    return response.data;
  } catch (error) {
    console.error('Error fetching carriers:', error);
    throw error;
  }
};

export default api;
